import $H from '@/common/lib/request.js';
import swiperImage from '@/components/index/swiper-image.vue';
import indexNav from '@/components/index/index-nav.vue';
import threeAdv from '@/components/index/three-adv.vue';
export default {
	components: {
		swiperImage,
		indexNav,
		threeAdv
	},
	data() {
		return {
			showStoreBox: true,
			scrollinto: 'tab1',
			scrollH: 500,
			tabIndex: 0,
			tabBars: [{ name: '首页推荐' }, { name: '店铺信息' }],
			dataList: {},
			advImages:[],
			shop:{
				shopImages:[],
				shopAddress:'',
				shopAdv:'',
				shopDetail:''
			}
			
		};
	},
	onPullDownRefresh() {
		this.__init()
	},
	onNavigationBarSearchInputClicked() {
		uni.navigateTo({
			url: '../search/search'
		});
	},
	created() {},
	onLoad() {
		// 获取可视区域高度
		uni.getSystemInfo({
			success: res => {




				let navbarH = uni.upx2px(90);

				this.scrollH = res.windowHeight - uni.upx2px(82) - navbarH;
			}
		});

		this.__init();
	},
	methods: {
		async __init() {
			
			let data = await $H.get('/product/product/index');
			if (data) {
				console.log(data);
				this.dataList = data;
			}
			
			$H.get("/admin/index/data/get").then(res=>{
				this.shop = res
				
			}).catch(()=>{
					
			})
				
			$H.get("/admin/index/adv/get").then(res=>{
				this.advImages = res
			}).catch(()=>{
					
			})
			
			uni.stopPullDownRefresh()
			
		},

		changeTab(index) {
			if (this.tabIndex === index) {
				return;
			}
			this.tabIndex = index;
			this.scrollinto = 'tab' + index;
		},
		// 监听滑动列表
		onChangeTab(e) {
			this.changeTab(e.detail.current);
			console.log(e.detail.current);
		},
		// 点击主页搜索框 跳转到搜索页面
		openSearch() {
			uni.navigateTo({
				url: '../search/search'
			});
		},
		addData() {
			let index = this.tabIndex;
		}
	}
};
